* {
  box-sizing: border-box;
  font-family: "ballo_paaji_2regular";
}

@font-face {
  font-family: "ballo_paaji_2regular";
  src: url("./assets/fonts/baloopaaji2-regular-webfont.woff2")
      format("woff2"),
      url("./assets/fonts/baloopaaji2-regular-webfont.woff")
      format("woff");
      font-weight: normal;
      font-style: normal;
}

ul,ol {
  list-style: none;
}

body {
  margin-top: 100px;
}
h1 {
  margin-top: 250px;
}
p.one {
  display: block;
  position: relative;
  margin-right: 0;
  right: 70px;
  bottom: 145px;
  height: 155px;
  width: 250px;
  opacity: 0%;
}
p.one:hover {
  color: rgb(2, 97, 205);
  border: 5px dotted #ff0;
  border-radius: 27px;
  opacity: 100%;
  display: block;
  height: 35px;
}
